@import "https://fonts.googleapis.com/css2?family=Pixelify+Sans:wght@400;500;600;700&display=swap";
:root {
  --primaryColor: #fff;
}

* {
  color: #fff;
  margin: 0;
  padding: 0;
  font-family: Pixelify Sans, sans-serif;
}

body {
  background: linear-gradient(to bottom right, #101010, #505050);
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.container-input {
  flex-direction: column;
  align-items: center;
  gap: 2rem;
