  gap: 5px;
  margin: 13rem 0 0;
  display: flex;
}

.title img {
  height: 60px;
}

.title span {
  padding-bottom: 5px;
  font-size: 40px;
  font-weight: 500;
}

.querre-icon {
  cursor: pointer;
}

.app {
  width: max-content;
  position: relative;
}

.app input {
  border: 2px solid var(--primaryColor);
  background-color: #151515;
  border-radius: 10px;
  width: 30em;
  height: 50px;
  padding: 0 15px;
  font-size: 15px;
}

.app input:focus {
  outline: 5px solid #5e5e5e;
}

.app button {
  cursor: pointer;
  background-color: var(--primaryColor);
  color: #000;
  border: none;
  border-radius: 7px;
  width: 7em;
  height: 40px;
  font-size: 15px;
  position: absolute;
  top: 50%;
  right: 7px;
  transform: translate(0%, -50%);
}

.app button:hover {
