  display: flex;
}

.header-container div img {
  height: 40px;
}

.header-container div span {
  padding-bottom: 4px;
  font-size: 25px;
}

.qr {
  justify-content: center;
  align-items: center;
  height: 380px;
  display: flex;
}

.shape {
  background-color: #5e5e5e;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 280px;
  height: 280px;
  display: flex;
}

#qrcode {
  background-color: #fff;
